(function () {
    function videoPlay(wrapper) {
        var iframe = wrapper.querySelector(".js-videoIframe");
        const src = wrapper.dataset.src;

        // hide poster
        wrapper.classList.add("videoWrapperActive");
        iframe.innerHTML =
            '<iframe autoplay="true" frameborder="0" allowTransparency="true" allowfullscreen allow="autoplay; fullscreen" src="https://player.vimeo.com/video/' +
            src +
            '?autoplay=1&title=0&byline=0&portrait=0&share=0"></iframe>';
    }

    // poster frame click event
	const playBtn = document.querySelector(".play-btn");
    playBtn.addEventListener("click", function (ev) {
        if (ev.target.parentElement.classList.contains("js-videoPoster")) {
            ev.preventDefault();
            var poster = ev.target;
            var wrapper = poster.closest(".js-videoWrapper");

            videoPlay(wrapper);
        }
    });
})();